<template>
  <!-- 根标签 -->
  <div class="Header">
    <!-- header 背景-->
    <div
      :class="['box', { closeHeader: status }]"
      :style="getBackground"
      @mousewheel.prevent="stop"
    >
      <!-- 包含显示内容的盒子  以及shadow -->
      <div :class="['show', { closeShow: status }]">
        <!-- 切换图片按钮 -->
        <div class="bgBtn">
          <div class="pre" @click="pre()">
            <svg
              class="bi bi-chevron-left"
              width="1em"
              height="1em"
              viewBox="0 0 16 16"
              fill="currentColor"
              xmlns="http://www.w3.org/2000/svg"
            >
              <path
                fill-rule="evenodd"
                d="M11.354 1.646a.5.5 0 0 1 0 .708L5.707 8l5.647 5.646a.5.5 0 0 1-.708.708l-6-6a.5.5 0 0 1 0-.708l6-6a.5.5 0 0 1 .708 0z"
                clip-rule="evenodd"
              />
            </svg>
          </div>
          <div class="next" @click="next()">
            <svg
              class="bi bi-chevron-right"
              width="1em"
              height="1em"
              viewBox="0 0 16 16"
              fill="currentColor"
              xmlns="http://www.w3.org/2000/svg"
            >
              <path
                fill-rule="evenodd"
                d="M4.646 1.646a.5.5 0 0 1 .708 0l6 6a.5.5 0 0 1 0 .708l-6 6a.5.5 0 0 1-.708-.708L10.293 8 4.646 2.354a.5.5 0 0 1 0-.708z"
                clip-rule="evenodd"
              />
            </svg>
          </div>
        </div>

        <!-- 第一行内容 -->
        <div class="row1">
          <h1>{{ getGreet }}</h1>
        </div>
        <!-- 第二行内容 -->
        <div class="row2">
          <p>welcome to korilo's blog</p>
        </div>
      </div>

      <!-- 下箭头提示 -->
      <div :class="['footer', { closeFooter: status }]">
        <svg
          class="bi bi-chevron-double-down"
          width="1em"
          height="1em"
          viewBox="0 0 16 16"
          fill="currentColor"
          xmlns="http://www.w3.org/2000/svg"
        >
          <path
            fill-rule="evenodd"
            d="M1.646 6.646a.5.5 0 0 1 .708 0L8 12.293l5.646-5.647a.5.5 0 0 1 .708.708l-6 6a.5.5 0 0 1-.708 0l-6-6a.5.5 0 0 1 0-.708z"
            clip-rule="evenodd"
          />
          <path
            fill-rule="evenodd"
            d="M1.646 2.646a.5.5 0 0 1 .708 0L8 8.293l5.646-5.647a.5.5 0 0 1 .708.708l-6 6a.5.5 0 0 1-.708 0l-6-6a.5.5 0 0 1 0-.708z"
            clip-rule="evenodd"
          />
        </svg>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "Header",
  props: {
    images: {
      type: Array
    }
  },
  data() {
    return {
      hour: 0,
      greeting: "",
      bg: 0,
      timer: "",
      status: false
    };
  },
  methods: {
    // clear() {
    //   alert("hello");
    //   clearInterval(this.timer);
    // },
    stop(event) {
      if (event.wheelDelta < 0 || event.detail > 0) {
        this.status = true;
      } else {
        this.status = false;
      }
    },
    pre() {
      this.bg--;
      clearInterval(this.timer);
      let that = this;
      this.timer = setInterval(function() {
        that.bg++;
      }, 5000);
    },
    next() {
      this.bg++;
      clearInterval(this.timer);
      let that = this;
      this.timer = setInterval(function() {
        that.bg++;
      }, 5000);
    }
  },
  computed: {
    getGreet() {
      if (this.hour > 17 || this.hour < 6) {
        return "Good Evening";
      } else if (this.hour < 13) {
        return "Good Morning";
      } else {
        return "Good Afternoon";
      }
    },
    getBackground() {
      if (this.bg > this.images.length - 1) {
        this.bg = 0;
      } else if (this.bg < 0) {
        this.bg = this.images.length - 1;
      }

      return "background-image: url(" + this.images[this.bg].url + ")";
    }
  },
  mounted() {
    let date = new Date();
    this.hour = date.getHours();
    let that = this;
    this.timer = setInterval(function() {
      that.bg++;
    }, 5000);
  }
};
</script>

<style scoped>
ul li {
  list-style: none;
}

.box {
  width: 100vw;
  height: 100vh;
  position: relative;
  background-size: cover;
  background-position: center 20%;
  transition: 0.9s;
  box-shadow: rgba(0, 0, 0, 0.7) 0px 10px 20px;
}
.closeHeader {
  height: 300px;
}

.bgBtn {
  height: 80px;
  width: 100%;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
}
.bgBtn .pre,
.bgBtn .next {
  height: 80px;
  width: 80px;
  font-size: 30px;
  position: absolute;
  text-align: center;
  line-height: 80px;
  color: white;
  transition: 0.5s;
}
.bgBtn .next {
  right: 0;
}
.bgBtn .pre:hover,
.bgBtn .next:hover {
  background-color: rgba(0, 0, 0, 0.5);
}
.show {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  margin: auto;
  display: flex;
  flex-direction: column;
  justify-content: center;
  background-color: rgba(0, 0, 0, 0.5);
  transition: 0.9s;
}
.closeShow {
  background-color: rgba(0, 0, 0, 0.7);
}
.row1 {
  display: flex;
  justify-content: center;
}
.row1 h1 {
  font: 50px "Comic Sans MS";
  color: whitesmoke;
}
.row2 {
  display: flex;
  justify-content: space-around;
  margin-top: 20px;
}
.row2 p {
  font-size: 30px;
  color: whitesmoke;
}
.footer {
  text-align: center;
  color: white;
  position: absolute;
  bottom: 0;
  margin-bottom: 100px;
  width: 100%;
  font-size: 30px;
  transition: 0.9s;
}
.closeFooter {
  color: transparent;
}
</style>



